<template>
  <div class="pagination">
    <button @click="btnPage">首页</button>
    <button @click="btnPage">上一页</button>
    <button v-if="expand" class="expand">.....</button>
    <button
      :class="[{ currentPage: page === currentBtn }, 'pagebtn']"
      v-for="page in pageBtn"
      :key="page"
      @click="btnPage(page)"
    >
      {{ page }}
    </button>
    <button v-if="expand" class="expand">.....</button>
    <button @click="btnPage">下一页</button>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
    data(){
        return {
            pageBtn:[1,2,3,4,5],
            currentBtn:1,
            expand:false
        }
    },
    methods:{
        btnPage(page){
            if(typeof page !== 'number'){
                switch(page.target.innerText){
                    case '上一页':
                        $('button.currentPage').prev().click();
                        break;
                    case '下一页':
                        $('button.currentPage').next().click()
                        break;
                    case '首页':
                        this.pageBtn = [1,2,3,4,5]
                        this.btnPage(1)
                        break;
                    default:
                        break        
                }
                return
            }
            this.currentBtn = page
            if(page > 4){
                this.expand = true
            }else{
                this.expand = false
            }
            if(page === this.pageBtn[4]){
                this.pageBtn.shift()
                this.pageBtn.splice(4,0,this.pageBtn[3]+1)
            }else if(page === this.pageBtn[0] && page !== 1){
                console.log(page)
                this.pageBtn.unshift(this.pageBtn[0]-1)
                this.pageBtn.splice(5,1)
            }
            this.$emit('henderList',this.currentBtn)
            console.log(this.currentBtn)
        }
    }
}
</script>

<style scoped>
.pagination {
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: white;
  padding: 6px 20px;
  border-radius: 5px;
  border: 1px solid #888;
}
button {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #778087;
  border-radius: 3px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 0 2px;
  width: 55px;
  height: 29px;
}
.pegebtn {
  position: relative;
  bottom: 1px;
  width: 40px;
  margin: 0 4px;
}
.currentPage {
  color: white;
  background-color: #1f1b1b;
}
.expend:hover{
    cursor:text;
}
</style>