<template>
  <div class="article">
    <div v-if="isLoading" class="loading">
        <img src="../assets/loading.gif" alt="">
    </div>
    <div v-else>
      <div class="topic_header">
        <div class="topic_title">{{ post.title }}</div>
        <ul>
          <li>发布于：{{ post.create_at | formatDate }}</li>
          <li>作者： {{ post.author.loginname }}</li>
          <li>{{ post.visit_count }}次浏览</li>
          <li>来自 {{ post | formatPost }}</li>
        </ul>
      </div>
      <div v-html="post.content" class="topic_content"></div>
    </div>
    <div id="reply">
    <div class="topbar">回复</div>
    <div
      v-for="(replies, index) in post.replies"
      :key="index.id"
      class="replySec"
    >
      <div class="replyUp">
        <router-link :to="{name:'user_info',params:{
            name:replies.author.loginname
        }}" >
          <img :src="replies.author.avatar_url" alt="" />
        </router-link>
        <!-- <router-link> -->
          <span>{{ replies.author.loginname }}</span>
        <!-- </router-link> -->

        <span> {{ index + 1 }}楼 </span>
        <span v-if="replies.ups.length > 0">
         ⭐ {{ replies.ups.length }}
        </span>
        <span v-else></span>
      </div>
      <p v-html="replies.content"></p>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      isLoading: false,
      post: {},
    };
  },
  methods: {
    getPost() {
      this.$axios(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        .then((response) => {
          if (response.data.success === true) {
            this.post = response.data.data;
            this.isLoading = false;
          }
        })
        .catch((err) => {
          console.log(err + "错误");
        });
    },
  },
  beforeMount() {
    this.getPost();
    this.isLoading = true;
  },
   watch:{
      "$route"(to,form){
          this.getPost()
      }
  }
};
</script>
<style >
  @import url('../assets/markdown-github.css');
  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    font-size: 12px;
    margin-top: 10px;
  }

  .article:not(:first-child) {
    margin-right: 340px;
    margin-top: 15px;
    border: 1px splid red;
  }

  #reply, .topic_header {
    background-color: #fff;
  }

  #reply {
    margin-top: 15px;
  }

  #reply img {
    width: 30px;
    height: 30px;
    position: relative;
    bottom: -9px;
  }

  #reply a, #reply span {
    font-size: 13px;
    color: #666;
    text-decoration: none;
  }
  .replySec{
    border-bottom:1px solid #e5e5e5;
    padding:0 10px;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }

  .replyUp a:nth-of-type(2) {
    margin-left: 0px;
    display: inline-block;
  }

  .topic_header {
    padding: 10px;
    background: #fff;
  }

  .topic_title {
    font-size: 20px;
    font-weight: bold;
    padding-top: 8px;
  }

  .topic_header ul {
    list-style: none;
    padding: 0px 0px;
    margin: 6px 0px;
  }

  .topic_header li {
    display: inline-block;
    font-size: 12px;
    color: #838383;
    margin-right: 6px;
  }

  .topic_content {
    border-top: 1px solid #e5e5e5;
    padding: 0 10px;
    /* border: 1px solid red; */
    background-color: #fff;
  }

  .markdown-text img {
    width: 92% !important;
  }
</style>