<template>
  <div class="UserInfo">
    <div v-if="isLoading">
      <img src="../assets/loading.gif" />
    </div>
    <div v-else class="userInfomation">
      <section>
        <img :src="userinfo.avatar_url" alt="" />
        <span>{{ userinfo.loginname }}</span>
        <p>{{ userinfo.score }}积分</p>
        <div>github名字：{{ userinfo.githubUsername }}</div>
        <span>注册时间：{{ userinfo.create_at | formatDate }}</span>
      </section>
      <div class="replies">
        <h4>最近创建的主题</h4>
        <ul>
          <li v-for="item in userinfo.recent_topics" :key="item.id">
            <router-link
              :to="{
                name: 'post_content',
                params: {
                  id: item.id,
                },
              }"
            >
              {{ item.title }}
            </router-link>
          </li>
        </ul>
      </div>
      <div class="topics">
        <h4>回复的主题</h4>
        <ul>
          <li v-for="item in userinfo.recent_replies" :key="item.id">
            <router-link
              :to="{
                name: 'post_content',
                params: {
                  id: item.id,
                },
              }"
            >
              {{ item.title }}
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoading: false,
      userinfo: {},
    };
  },
  methods: {
    getPost() {
      this.$axios
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then((res) => {
          this.userinfo = res.data.data;
          this.isLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  beforeMount() {
    this.isLoading = true;
    this.getPost();
  },
};
</script>

<style scoped>
  .userInfomation {
    background: white;
    width: 75%;
    /* margin: 10px 0; */
    border-radius: 6px;
  }
  .userInfomation section {
    padding: 12px;
  }
  .userInfomation img {
    width: 30px;
  }
  .userInfomation li {
    list-style:none;
  }
  .userInfomation .replies,
  .userInfomation .topics {
    font-size: 0.72rem;
    border-top: 10px #DDDDDD solid;
  }
  .userInfomation > div >ul > li {
    padding: 4px 0 4px 12px;
    white-space: nowrap;
    font-size: 0.72rem;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 30px;
    vertical-align: middle;
    border-bottom: 1px solid #f0f0f0;
  }
  .userInfomation > div >ul > li > a {
    color: #094E99;
    text-decoration: none;
  }
  .replies h4 ,
  .topics h4{
    /* border: 1px solid red; */
    font-size: 16px;
    padding: 8px;
    background-color:#f6f6f6;
  }
</style>