<template>
  <div class="postList">
    <div v-if="isLoading" class="loading">
      <img src="../assets/loading.gif" alt="">
    </div>
    <div v-else class="posts">
      <ul class="list">
        <li class="toobar">
          <span>全部</span>
          <span>精华</span>
          <span>分享</span>
          <span>问答</span>
          <span>招聘</span>
        </li>
        <li v-for="(item, index) in posts" :key="index">
          <!-- 头像 -->
          <img :src="item.author.avatar_url" />
          <span class="allcount">
            <!-- 回复/浏览 -->
            <span class="reply_count">{{ item.reply_count }}</span>
            / {{ item.visit_count }}
          </span>
          <span
            :class="[
              {
                put_good: item.good === true,
                put_top: item.good === true,
                'topiclist-tab': item.good !== true && item.top !== true,
              },
            ]"
          >
            <span>
              {{ item | formatPost }}
            </span>
          </span>
          <router-link :to="{name:'post_content',params:{
              id:item.id,
              name:item.author.loginname
          }}" >
            <!-- 文章标题 -->
            <span>{{ item.title }}</span>
          </router-link>

          <!-- 最终回复时间 -->
          <span class="last_reply">
            {{ item.last_reply_at | formatDate }}
          </span>
        </li>
        <li>
          <Pagination  @henderList="renderList"></Pagination>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Pagination from './Pagination.vue'
export default {
  components:{Pagination},
  data() {
    return {
      isLoading: false,
      posts: [],
      postList:1
    };
  },
  methods: {
    getPost() {
      this.$axios
        .get("https://cnodejs.org/api/v1/topics",{
          params:{
            page:this.postList,
            limit:30
          }
        })
        .then((response) => {
          this.isLoading = false;
          this.posts = response.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    renderList(value){
      this.postList = value
      this.getPost()
      // alert(value)
      // console.log(value)
    }
  },
  beforeMount() {
    this.isLoading = true;
    this.getPost();
  },
};
</script>

<style scoped >
.postList {
  background-color: #e1e1e2;
}
.post {
  margin-top: 10px;
}
.postList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}
ul {
  list-style: none;
  width: 100%;
  /* max-width: 1344px; */
  margin: 0 auto;
}
ul li:not(:first-child) {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma,
    "Hiragino Sans GB", STHeiti, sans-serif !important;
  color: #333;
  border-top: 1px solid #f0f0f0;
  background-color: white;
}
li:not(:first-child):hover {
  background-color: #f5f5f5;
}
li span {
  line-height: 30px;
}
.allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}
.reply_count {
  color: #9e78c0;
  font-size: 14px;
}
.put_good,
.put_top {
  background-color: #80bd01;
  padding: 2px 4px;
  border-radius: 10px;
  font-size: 14px;
  margin-right: 12px;
}
.topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -o-border-radius: 4px;
  font-size: 12px;
  margin-right: 10px;
}
.last_reply {
  text-align: right;
  min-width: 50px;
  white-space: nowrap;
  float: right;
  color: #778087;
  font-size: 14px;
}
.toobar {
  height: 40px;
  background-color: #f5f5f5;
}
.toobar span {
  font-size: 14px;
  color: #80bd01;
  margin: 0 12px;
  cursor: pointer;
}
.toobar span:hover {
  color: #005580;
}
a {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: underline;
}
.loading {
  text-align: center;
  padding-top: 300px;
}
</style>