<template>
  <div class="autherinfo">
    <div class="authersummay">
      <div class="topbar">作者</div>
      <router-link
        :to="{
          name: 'user_info',
          params: {
            name: userinfo.loginname,
          },
        }"
      >
        <img :src="userinfo.avatar_url" alt="" />
      </router-link>
      <span>{{ userinfo.loginname }}</span>
      <p>积分:{{ userinfo.score }}</p>
    </div>
    <div class="recent_topics">
      <div class="topbar">作者最近主题</div>
      <ul>
        <li v-for="list in topicLength5" :key="list.id">
          <router-link
            :to="{
              name: 'post_content',
              params: {
                id: list.id,
                name: list.author.loginname,
              },
            }"
          >
            {{ list.title }}
          </router-link>
        </li>
      </ul>
    </div>
    <div class="recent_replies">
      <div class="topbar">最近回复</div>
      <ul>
        <li v-for="list in recentLength5" :key="list.id">
            <router-link :to="{name:'post_content',params:{
                id:list.id,
                name: list.author.loginname
            }}">
                {{ list.title }}        
            </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userinfo: {},
    };
  },
  methods: {
    getPost() {
      this.$axios(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then((res) => {
          this.userinfo = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  computed: {
    topicLength5() {
      if (this.userinfo.recent_topics) {
        return this.userinfo.recent_topics.slice(0, 5);
      } else {
        return this.userinfo.recent_topics;
      }
    },
    recentLength5() {
      if (this.userinfo.recent_replies) {
        return this.userinfo.recent_replies.slice(0, 5);
      } else {
        return this.userinfo.recent_replies;
      }
    },
  },
  beforeMount() {
    this.getPost();
  },
 
};
</script>

<style scoped>
.authersummay,
.recent_replies,
.recent_topics {
  background-color: #fff;
}
.autherinfo {
  width: 328px;
  float: right;
}
li {
  padding: 6px 0;
  border-bottom: 1px solid #e5e5e5;
  font-size: 12px;
  padding-left: 14px;
}

ul a {
  font-size: 12px;
  text-decoration: none;
  color: #778087;
}

.topbar {
  font-size: 16px;
}

img {
  height: 48px;
  width: 48px;
  border-radius: 3px;
  margin: 10px;
}

.loginname {
  width: 100px;
  float: right;
  margin-top: 22px;
  margin-right: 159px;
  font-size: 14px;
}

.loginname a {
  text-decoration: none;
  color: #778087;
}

.authersummay .topbar {
  margin-top: 0px;
}
</style>